<template>
  <div :class="['st-reader', { 'dark-mode': isDarkMode }]">

    <div v-if="viewMode === 'home'" class="upload-area">
      <div class="upload-container">
        <div class="upload-card single-upload">
          <input type="file" ref="fileInput" @change="handleFileUpload" accept=".jsonl" id="file-input"
            class="file-input" />
          <label for="file-input" class="upload-label">
            <Icon icon="bi:file-earmark-text" class="upload-icon" />
            <div class="upload-text"></div>
            <div class="upload-hint"> .jsonl </div>
          </label>
        </div>

        <div class="upload-card zip-upload">
          <input type="file" ref="zipInput" @change="handleZipUpload" accept=".zip" id="zip-input" class="file-input" />
          <label for="zip-input" class="upload-label">
            <Icon icon="bi:file-earmark-zip" class="upload-icon" />
            <div class="upload-text"></div>
            <div class="upload-hint"> ZIP     </div>
          </label>
        </div>

        <div v-if="loadingStatus" class="loading-status">
          <div class="spinner"></div>
          {{ loadingStatus }}
        </div>
      </div>
    </div>

    <div v-else-if="viewMode === 'dashboard'" class="dashboard-container">
      <div class="dashboard-header">
        <div class="header-column">
          <button @click="resetReader" class="back-btn">
            <Icon icon="akar-icons:arrow-left" /> 
          </button>
          
          <div class="header-title-row">
            <h1></h1>
            <div class="dashboard-meta">
                {{ dashboardData.totalFiles }} 
            </div>
          </div>
        </div>
      </div>

      <div class="dashboard-content">
        <div class="stats-overview">
          <div class="stat-hero">
            <div class="hero-value">{{ dashboardData.totalEffectiveCN.toLocaleString() }}</div>
            <div class="hero-label"> ()</div>
          </div>
          
          <div class="stat-divider"></div>

          <div class="stat-sub-row">
            <div class="sub-stat">
              <div class="sub-value">{{ Object.keys(dashboardData.groups).length }}</div>
              <div class="sub-label"></div>
            </div>

            <div class="sub-stat">
              <div class="sub-value">{{ dashboardData.totalTurns.toLocaleString() }}</div>
              <div class="sub-label"></div>
            </div>

            <div class="sub-stat">
              <div class="sub-value">{{ dashboardData.totalRerolls.toLocaleString() }}</div>
              <div class="sub-label">Roll</div>
            </div>
          </div>
        </div>

        <div class="dashboard-calendar-section" v-if="dashboardCalendar.months.length > 0">
          <div class="dash-cal-header">
            <div class="dash-cal-title">
              <Icon icon="bi:calendar-check" /> 
            </div>
            <div class="dash-cal-meta">
               {{ dashboardCalendar.activeDays }}    {{ dashboardCalendar.totalMessages }} 
            </div>
          </div>

          <div class="calendar-widget dashboard-widget">
            <div class="calendar-nav">
              <button @click="prevDashMonth"
                :disabled="dashboardCalendar.currentMonthIdx >= dashboardCalendar.months.length - 1"
                class="nav-btn"></button>

              <div class="current-month-label">
                {{ dashboardCalendar.months[dashboardCalendar.currentMonthIdx].year }}
                {{ dashboardCalendar.months[dashboardCalendar.currentMonthIdx].month }}
              </div>

              <button @click="nextDashMonth" :disabled="dashboardCalendar.currentMonthIdx <= 0"
                class="nav-btn"></button>
            </div>

            <div class="month-card single-view">
              <div class="month-grid">
                <div class="weekday-header"></div>
                <div class="weekday-header"></div>
                <div class="weekday-header"></div>
                <div class="weekday-header"></div>
                <div class="weekday-header"></div>
                <div class="weekday-header"></div>
                <div class="weekday-header"></div>

                <div v-for="n in dashboardCalendar.months[dashboardCalendar.currentMonthIdx].paddingStart"
                  :key="'pad-' + n" class="day-cell padding"></div>

                <div v-for="day in dashboardCalendar.months[dashboardCalendar.currentMonthIdx].days" :key="day.dateStr"
                  :class="['day-cell', { 'has-data': day.count > 0 }]" :style="{ backgroundColor: day.bgStyle }"
                  @mouseenter="showDayTooltip($event, day)" @mouseleave="hideDayTooltip"
                  @mousemove="moveDayTooltip($event)">
                  <span class="day-number">{{ day.dayNum }}</span>
                </div>
              </div>
            </div>

            <div class="month-footer-stats">
              <div class="footer-stat">
                <span class="f-label"></span>
                <span class="f-value">{{
                  dashboardCalendar.months[dashboardCalendar.currentMonthIdx].totalCount.toLocaleString() }}</span>
              </div>
              <div class="footer-divider"></div>
              <div class="footer-stat">
                <span class="f-label"></span>
                <span class="f-value">{{
                  dashboardCalendar.months[dashboardCalendar.currentMonthIdx].totalChars.toLocaleString() }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="filter-bar">
          <input v-model="dashboardSearch" type="text" placeholder="..." class="dash-search-input" />
        </div>

        <div class="character-groups">
          <div v-for="(files, charName) in filteredDashboardGroups" :key="charName" class="char-group">
            <div class="char-header">
              <div class="char-header-left">
                <div class="char-name">
                  <Icon icon="bxs:user" class="char-icon" />
                  {{ charName }}
                </div>
                <div class="char-stats">
                  {{ files.length }}   {{ (files.reduce((a, b) => a + b.effectiveCN, 0)).toLocaleString() }} 
                </div>
              </div>
              
              <button @click.stop="openDashboardIntimacy(charName, files)" class="action-button intimacy-btn icon-only-btn" title="">
                <Icon icon="bxs:heart" class="heart-icon" />
              </button>
            </div>

            <div class="file-grid">
              <div v-for="file in files" :key="file.fileName" class="file-card" @click="openChatFromDashboard(file)">
                <div class="file-info">
                  <div class="file-name" :title="file.fileName">{{ file.fileName }}</div>
                  <div class="file-meta">
                    <span class="meta-tag cn">{{ file.effectiveCN.toLocaleString() }} </span>
                    <span class="meta-tag turns">{{ file.totalTurns }} </span>
                  </div>
                </div>
                <div class="file-date">{{ file.lastDate }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


    <div v-else class="chat-container">
      <div class="chat-header">
        <div class="header-content">
          <button v-if="fromDashboard" @click="backToDashboard" class="back-link-btn">
            <Icon icon="akar-icons:arrow-left" /> 
          </button>

          <h1 class="chat-title">{{ metadata.character_name || '' }}</h1>
          <div class="chat-meta">
            <span v-if="metadata.create_date">{{ metadata.create_date }}</span>
            <span class="message-count">{{ messages.length }} </span>
          </div>
        </div>
        <div class="header-actions">
          <button @click="toggleSearchBar" :class="['action-button', 'search-toggle', { 'active': showSearchBar }]">
            
          </button>
          <button @click="toggleTagFilterManager" :class="['action-button', { 'active': showTagFilterManager }]">
             <span v-if="tagFilters.length">({{tagFilters.filter(f => !f.disabled).length}})</span>
          </button>
          <button @click="toggleRegexManager" class="action-button regex-button">
             <span v-if="regexScripts.length">({{regexScripts.filter(s => !s.disabled).length}})</span>
          </button>
          <div class="header-actions">
            <button @click="toggleFavoritesPanel" :class="['action-button', { 'active': showFavoritesPanel }]">
               <span v-if="favorites.length">({{ favorites.length }})</span>
            </button>
            <button @click="openIntimacyModal" class="action-button intimacy-btn">
              <Icon icon="bxs:heart" class="heart-icon" /> 
            </button>
            <button @click="toggleReadingMode"
              :class="['action-button', 'reading-mode-btn', { 'active': readingMode }]">
              
            </button>
            <button @click="openExportRangeDialog" class="action-button">
              
            </button>
          </div>
          <button @click="toggleStylePanel" :class="['action-button', { 'active': showStylePanel }]">
            
          </button>
          <button @click="toggleDarkMode" class="action-button mode-toggle">
            <div class="icon-label-row">
              <template v-if="isDarkMode">
                <Icon icon="heroicons-solid:sun" class="mode-icon sun" />
                <span></span>
              </template>

              <template v-else>
                <Icon icon="bxs:moon" class="mode-icon moon" />
                <span></span>
              </template>
            </div>
          </button>
          <button @click="resetReader" class="reset-button"></button>
        </div>
      </div>

      <div v-if="showSearchBar" class="search-bar">
        <div class="search-input-wrapper">
          <input v-model="searchQuery" @input="onSearchInput" type="text" class="search-input"
            placeholder="..." />
          <button v-if="searchQuery" @click="clearSearch" class="search-clear" title=""></button>
        </div>
        <div class="search-info">
          <span v-if="searchQuery">
             {{ filteredMessages.length }} 
          </span>
        </div>
      </div>

      <div v-if="showTagFilterManager" class="regex-manager tag-filter-manager">
        <div class="regex-header">
          <h2></h2>
          <div class="regex-actions">
            <button @click="addNewTagFilter" class="btn btn-primary"></button>
          </div>
        </div>

        <div v-if="tagFilterForm.id" class="script-form">
          <div class="form-group">
            <label></label>
            <input v-model="tagFilterForm.name" type="text" placeholder="" />
          </div>
          <div class="form-group">
            <label></label>
            <input v-model="tagFilterForm.tagName" type="text" placeholder="think  thinking" />
            <div class="form-hint">think,thinking,disclaimer</div>
          </div>
          <div class="form-group">
            <label></label>
            <select v-model="tagFilterForm.mode" class="form-select">
              <option value="remove"></option>
              <option value="keep"></option>
              <option value="unwrap"></option>
            </select>
          </div>
          <div class="form-group checkbox-group">
            <label>
              <input v-model="tagFilterForm.disabled" type="checkbox" />
              
            </label>
          </div>
          <div class="form-buttons">
            <button @click="cancelEditTagFilter" class="btn btn-secondary"></button>
            <button @click="saveTagFilter" class="btn btn-primary"></button>
          </div>
        </div>

        <div class="script-list">
          <div v-if="!tagFilters.length" class="no-scripts">
            ""
          </div>
          <div v-for="(filter, index) in tagFilters" :key="filter.id"
            :class="['script-item', { 'disabled': filter.disabled }]">
            <div class="script-info">
              <div class="script-name">{{ filter.name }}</div>
              <div class="script-regex">
                {{ filter.mode === 'remove' ? '' : filter.mode === 'keep' ? '' : '' }}:
                &lt;{{ filter.tagName }}&gt;
              </div>
            </div>
            <div class="script-controls">
              <button @click="moveTagFilterUp(index)" :disabled="index === 0" class="btn-icon" title=""></button>
              <button @click="moveTagFilterDown(index)" :disabled="index === tagFilters.length - 1" class="btn-icon"
                title=""></button>
              <button @click="toggleTagFilter(filter)" :class="['btn-toggle', { 'active': !filter.disabled }]">
                {{ filter.disabled ? '' : '' }}
              </button>
              <button @click="editTagFilter(filter)" class="btn-icon" title=""></button>
              <button @click="deleteTagFilter(filter.id)" class="btn-icon btn-danger" title=""></button>
            </div>
          </div>
        </div>
      </div>

      <div v-if="showRegexManager" class="regex-manager">
        <div class="regex-header">
          <h2></h2>
          <div class="regex-actions">
            <button @click="importScripts" class="btn btn-secondary">JSON</button>
            <button @click="importFromCardOrPreset" class="btn btn-secondary">/</button>
            <button @click="exportScripts" class="btn btn-secondary" :disabled="!regexScripts.length"></button>
            <button @click="addNewScript" class="btn btn-primary"></button>
          </div>
        </div>

        <div v-if="scriptForm.id" class="script-form">
          <div class="form-group">
            <label></label>
            <input v-model="scriptForm.scriptName" type="text" placeholder="" />
          </div>
          <div class="form-group">
            <label></label>
            <textarea v-model="scriptForm.findRegex" placeholder="/(<think>[\s\S]*?</think>)/gs" rows="3"></textarea>
            <div class="form-hint">/pattern/flags  g </div>
          </div>
          <div class="form-group">
            <label></label>
            <input v-model="scriptForm.replaceString" type="text" placeholder="" />
          </div>
          <div class="form-group checkbox-group">
            <label>
              <input v-model="scriptForm.disabled" type="checkbox" />
              
            </label>
          </div>
          <div class="form-buttons">
            <button @click="cancelEdit" class="btn btn-secondary"></button>
            <button @click="saveScript" class="btn btn-primary"></button>
          </div>
        </div>

        <div class="script-list">
          <div v-if="!regexScripts.length" class="no-scripts">
            """"
          </div>
          <div v-for="(script, index) in regexScripts" :key="script.id"
            :class="['script-item', { 'disabled': script.disabled, 'dragging': dragIndex === index }]" draggable="true"
            @dragstart="handleDragStart(index)" @dragover="handleDragOver" @drop="(e) => handleDrop(e, index)"
            @dragend="handleDragEnd">
            <div class="script-drag-handle"></div>
            <div class="script-info">
              <div class="script-name">{{ script.scriptName }}</div>
              <div class="script-regex">{{ script.findRegex.substring(0, 60) }}{{ script.findRegex.length > 60 ? '...'
                :
                '' }}
              </div>
            </div>
            <div class="script-controls">
              <button @click="moveScriptUp(index)" :disabled="index === 0" class="btn-icon" title=""></button>
              <button @click="moveScriptDown(index)" :disabled="index === regexScripts.length - 1" class="btn-icon"
                title=""></button>
              <button @click="toggleScript(script)" :class="['btn-toggle', { 'active': !script.disabled }]">
                {{ script.disabled ? '' : '' }}
              </button>
              <button @click="editScript(script)" class="btn-icon" title=""></button>
              <button @click="deleteScript(script.id)" class="btn-icon btn-danger" title=""></button>
            </div>
          </div>
        </div>
      </div>

      <div v-if="showFavoritesPanel" class="regex-manager favorites-panel">
        <div class="regex-header">
          <h2></h2>
          <div class="regex-actions">
            <button @click="exportFavorites" class="btn btn-secondary" :disabled="!favorites.length"></button>
            <button @click="clearAllFavorites" class="btn btn-secondary" :disabled="!favorites.length"></button>
          </div>
        </div>
        <div class="script-list favorites-list">
          <div v-if="!favorites.length" class="no-scripts">
              ""
          </div>
          <div v-for="fav in favorites" :key="fav.id" class="favorite-item" @click="navigateToFavorite(fav)"
            title="">
            <div class="favorite-content">
              <div class="favorite-type">
                <template v-if="fav.type === 'message'">
                  <Icon icon="icon-park-twotone:copy" style="vertical-align: -2px;" /> 
                </template>
                <template v-else>
                  <Icon icon="ri:draft-line" style="vertical-align: -2px;" /> 
                </template>
                #{{ fav.messageIndex + 1 }}
              </div>
              <div class="favorite-text">{{ fav.text.substring(0, 100) }}{{ fav.text.length > 100 ? '...' : '' }}
              </div>
              <div class="favorite-meta">
                <span v-if="fav.speaker">{{ fav.speaker }}</span>
                <span>{{ formatFavoriteTime(fav.createdAt) }}</span>
              </div>
            </div>
            <div class="favorite-actions">
              <button @click.stop="copyFavorite(fav)" class="btn-icon" title="">
                <Icon icon="icon-park-twotone:copy" style="vertical-align: -2px;" />
              </button>
              <button @click.stop="deleteFavorite(fav.id)" class="btn-icon btn-danger" title=""></button>
            </div>
          </div>
        </div>
      </div>

      <div v-if="showStylePanel" class="regex-manager style-panel">
        <div class="regex-header">
          <h2></h2>
          <div class="regex-actions">
            <button @click="resetStyles" class="btn btn-secondary"></button>
          </div>
        </div>

        <div class="style-settings">
          <div class="style-group font-group">
            <label class="style-label"></label>
            <div class="style-flex-row">
              <select v-model="textStyles.fontFamily" @change="saveStylesToStorage" class="style-select">
                <optgroup label="">
                  <option value="system"></option>
                  <option value="serif"> / </option>
                  <option value="sans-serif"> / </option>
                  <option value="kaiti"></option>
                  <option value="fangsong"></option>
                  <option value="monospace"></option>
                </optgroup>
                <optgroup label="">
                  <option value="alegreya">Alegreya</option>
                  <option value="cangeryunhei"></option>
                  <option value="huiwenmingchao"></option>
                  <option value="kongmingchao">/</option>
                  <option value="pingxianzhensong"></option>
                  <option value="wenyueminguofangsong"></option>
                </optgroup>
                <optgroup v-if="customFonts.length" label="">
                  <option v-for="font in customFonts" :key="font.id" :value="'custom-' + font.id">
                    {{ font.name }}
                  </option>
                </optgroup>
              </select>
              <button @click="showCustomFontDialog = true" class="btn btn-secondary btn-sm compact-btn" title="">
                + 
              </button>
            </div>
          </div>

          <div class="sliders-grid">
            <div class="style-group compact-group">
              <label class="style-label"> / {{ textStyles.fontSize }}px</label>
              <input type="range" v-model.number="textStyles.fontSize" @input="handleStyleChange" min="12" max="32"
                step="1" class="style-slider" />
            </div>

            <div class="style-group compact-group">
              <label class="style-label"> / {{ textStyles.lineHeight.toFixed(1) }}</label>
              <input type="range" v-model.number="textStyles.lineHeight" @input="handleStyleChange" min="1.2" max="3"
                step="0.1" class="style-slider" />
            </div>

            <div class="style-group compact-group">
              <label class="style-label"> / {{ textStyles.paragraphSpacing.toFixed(1) }}em</label>
              <input type="range" v-model.number="textStyles.paragraphSpacing" @input="handleStyleChange" min="0"
                max="2" step="0.1" class="style-slider" />
            </div>

            <div class="style-group compact-group">
              <label class="style-label"> / {{ textStyles.letterSpacing }}px</label>
              <input type="range" v-model.number="textStyles.letterSpacing" @input="handleStyleChange" min="-2" max="10"
                step="0.5" class="style-slider" />
            </div>
          </div>

          <hr class="style-divider">

          <div class="color-grid">
            <div class="style-group compact-group">
              <label class="style-label"></label>
              <div class="color-picker-row">
                <input type="color" v-model="textStyles.textColor" @change="saveStylesToStorage" class="color-input">
                <span class="color-value">{{ textStyles.textColor }}</span>
              </div>
            </div>

            <div class="style-group compact-group">
              <label class="style-label"> (*text*)</label>
              <div class="color-picker-row">
                <input type="color" v-model="textStyles.italicColor" @change="saveStylesToStorage" class="color-input">
                <span class="color-value">{{ textStyles.italicColor }}</span>
              </div>
            </div>

            <div class="style-group compact-group">
              <label class="style-label"> (&lt;u&gt;)</label>
              <div class="color-picker-row">
                <input type="color" v-model="textStyles.underlineColor" @change="saveStylesToStorage"
                  class="color-input">
                <span class="color-value">{{ textStyles.underlineColor }}</span>
              </div>
            </div>

            <div class="style-group compact-group">
              <label class="style-label"> ( > )</label>
              <div class="color-picker-row">
                <input type="color" v-model="textStyles.quoteColor" @change="saveStylesToStorage" class="color-input">
                <span class="color-value">{{ textStyles.quoteColor }}</span>
              </div>
            </div>
          </div>

          <hr class="style-divider">

          <div class="style-group">
            <label class="style-label"></label>
            <div class="align-options">
              <button @click="setTextAlign('left')"
                :class="['align-btn', { 'active': textStyles.textAlign === 'left' }]" title=""></button>
              <button @click="setTextAlign('justify')"
                :class="['align-btn', { 'active': textStyles.textAlign === 'justify' }]" title=""></button>
              <button @click="setTextAlign('center')"
                :class="['align-btn', { 'active': textStyles.textAlign === 'center' }]" title=""></button>
            </div>
          </div>
        </div>

        <div class="style-preview">
          <div class="style-preview-label"></div>
          <div class="style-preview-content" :style="getPreviewStyles()">
            <p>
              
            </p>

            <p>
              <em>**</em>
            </p>

            <p>
               <u></u>
            </p>

            <blockquote>
              <br>
              <br>
              
            </blockquote>
          </div>
        </div>

        <div v-if="customFonts.length" class="custom-fonts-section">
          <div class="style-label"></div>
          <div class="custom-font-list">
            <div v-for="font in customFonts" :key="font.id" class="custom-font-item">
              <span class="custom-font-name">{{ font.name }}</span>
              <button @click="deleteCustomFont(font.id)" class="btn-icon btn-danger" title=""></button>
            </div>
          </div>
        </div>
      </div>

      <div v-if="showCustomFontDialog" class="modal-overlay" @click.self="showCustomFontDialog = false">
        <div class="modal-dialog custom-font-dialog">
          <div class="modal-header">
            <h3></h3>
            <button @click="showCustomFontDialog = false" class="modal-close"></button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label> <span class="required">*</span></label>
              <input v-model="customFontForm.name" type="text" placeholder="" class="form-input" />
            </div>

            <div class="import-tabs">
              <div class="import-method">
                <div class="import-method-header">URL</div>
                <input v-model="customFontForm.url" type="text" placeholder="URL ttf/otf/woff/woff2"
                  class="form-input" :disabled="!!customFontForm.file" />
              </div>

              <div class="import-divider"></div>

              <div class="import-method">
                <div class="import-method-header"></div>
                <input ref="fontFileInput" type="file" accept=".ttf,.otf,.woff,.woff2" @change="handleFontFileSelect"
                  class="form-file-input" :disabled="!!customFontForm.url" />
                <div v-if="customFontForm.file" class="file-selected">
                  : {{ customFontForm.file.name }}
                </div>
              </div>
            </div>

            <div class="form-hint">
              TTFOTFWOFFWOFF2<br>
              Base64
            </div>
          </div>
          <div class="modal-footer">
            <button @click="resetCustomFontForm" class="btn btn-secondary"></button>
            <button @click="addCustomFont" class="btn btn-primary"></button>
          </div>
        </div>
      </div>

      <div v-if="highlightMenu.show" class="highlight-menu"
        :style="{ left: highlightMenu.x + 'px', top: highlightMenu.y + 'px' }">
        <button @click="removeHighlightFromMenu" class="selection-btn"> </button>
      </div>

      <div v-if="!readingMode" class="messages-wrapper" :style="getContentStyles()">
        <div v-for="(message, index) in paginatedMessages" :key="currentRange.start + index"
          :class="['message-block', { 'user-message': message.is_user }]">
          <div class="message-header">
            <span class="speaker-name">{{ message.name }}</span>
            <div class="message-info">
              <span v-if="message.send_date" class="timestamp">{{ message.send_date }}</span>
              <span v-if="message.model" class="model-tag">{{ message.model }}</span>
              <button @click="toggleFavoriteMessage(getGlobalMessageIndex(index), message)"
