  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.header-title-row {
  display: flex;
  justify-content: space-between; /*  */
  align-items: center; /*  */
  width: 100%;
}

/* PC  */
.header-title-row h1 {
  font-size: 2rem;
  font-weight: 800;
  margin: 0;
