  display: flex;
  flex-direction: column; /*  */
  gap: 1.5rem; /*  */
  align-items: center;
}

/*  */
.upload-card {
  flex: 1;
  min-width: 320px;
  max-width: 350px;
  position: relative;
}

.file-input {
  display: none;
}

.upload-label {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 300px;
  /*  */
  padding: 2rem;
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid #000;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  text-align: center;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  color: #1a1a1a;
}

.upload-label:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  background: #000;
  color: #fff;
  border-color: #000;
}

/* ===  === */
.loading-status {
  display: flex;
  flex-direction: column; /*  */
  align-items: center;    /*  */
  justify-content: center;
  margin-top: 2rem;       /*  */
  color: #666;
  font-size: 0.9rem;
  font-weight: 600;
  gap: 0.75rem;           /*  */
  animation: fadeIn 0.3s ease;
}

/*  */
.spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #eee;      /*  */
  border-top: 3px solid #000;  /*  */
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(5px); }
  to { opacity: 1; transform: translateY(0); }
}

/*  */
.dark-mode .loading-status {
  color: #aaa;
}

.dark-mode .spinner {
  border-color: #333;
  border-top-color: #e0e0e0;
}

/*  */
