  justify-content: flex-end;
  gap: 0.5rem;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #eee;
}

/*  */
.script-list {
  max-height: 400px;
  overflow-y: auto;
}

.no-scripts {
  padding: 2rem;
  text-align: center;
  color: #666;
  font-size: 0.875rem;
}

.script-item {
  display: flex;
  align-items: center;
